.myModalSection {
  float: left;

  width: 50%;
  height: 100%;
  /* background-color: #006dbc; */
  /* top: 30%;
  transform: translate(0, 20%); */
  /* transform: translate(0px, 10%); */
}

.myModelSectionCenter {
  -webkit-transform: translate(15%, 30%);
          transform: translate(15%, 30%);
}

/* Modal Content (image) */
.modal-img {
  margin: 0;
  display: block;
  width: 70%;
  height: 70%;
  border-radius: 5vw;
}

/* Caption of Modal Image */
#caption {
  display: block;
  width: 70%;
  /* background-color: #ccc; */
  /* max-width: 700px; */
  text-align: center;
  color: #ccc;
  font-size: 50px;
  font-weight: 900;
  /* padding: 10px 0; */
  /* height: 150px; */
}

/* Add Animation */
.modal-img,
#caption {
  -webkit-animation-name: zoom;
  -webkit-animation-duration: 0.6s;
  animation-name: zoom;
  animation-duration: 0.6s;
}

@-webkit-keyframes zoom {
  from {
    -webkit-transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
  }
}

@keyframes zoom {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
